.inputForm
{
    background-color: #2fc5f3;
    width: 400px;
    margin: 0 auto;
    box-shadow: 1px 6px 46px 0px rgba(54,54,54,0.75);
    margin-top: 50px;
    padding: 20px;
    border-radius: 5px;
}
.inputForm input,textarea{
    width: 300px;
    border: 1px solid white;
    padding: 10px 10px;
    outline: none;
    margin-bottom: 20px;
    font-size: 16px;
    color: green;
}
button
{
    background-color: navy;
    color: white;
    font-weight: bold;
    margin-left: 100px;
    padding: 10px;
    border-radius: 4px;
}